import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      meta: {title: '商城首页',keepAlive:true},
      component: resolve => require(['../components/home/home.vue'], resolve)
    },
    {
      path: '/classify',
      name: 'Classify',
      meta: {title: '分类详情',keepAlive:false},
      component: resolve => require(['../components/classify/classify.vue'], resolve)
    },
    {
      path: '/search',
      name: 'Seacrh',
      meta: {title: '搜索页面',keepAlive:false},
      component: resolve => require(['../components/search/search.vue'], resolve)
    },
    {
      path: '/searchDetail',
      name: 'SearchDetail',
      meta: {title: '搜索结果',keepAlive:false},
      component: resolve => require(['../components/search-details/search-details.vue'], resolve)
    },
    {
      path: '/commodityDetails',
      name: 'commodityDetails',
      meta: {title: '商品详情页' ,keepAlive:false},
      component: resolve => require(['../components/commodity-details/commodity-details.vue'], resolve)
    },
    {
      path: '/shopCar',
      name: 'shopCar',
      meta: {title: '购物车',keepAlive:false},
      component: resolve => require(['../components/shop-car/shop-car.vue'], resolve)
    },
    {
      path: '/purchaseNow',
      name: 'purchaseNow',
      meta: {title: '提交订单',keepAlive:false},
      component: resolve => require(['../components/purchase-now/purchase-now.vue'], resolve)
    },
    {
      path: '/orderForm',
      name: 'orderForm',
      meta: {title: '我的订单',keepAlive:false},
      component: resolve => require(['../components/order-form/order-form.vue'], resolve)
    },
    {
      path: '/successPage',
      name: 'successPage',
      meta: {title: '成功',keepAlive:false},
      component: resolve => require(['../components/success-page/success-page.vue'], resolve)
    },{
      path: '/orderDetail',
      name: 'orderDetail',
      meta: {title: '订单详情',keepAlive:false},
      component: resolve => require(['../components/order-detail/order-detail.vue'], resolve)
    },{
      path: '/know',
      name: 'know',
      meta: {title: '购物须知',keepAlive:false},
      component: resolve => require(['../components/know/know.vue'], resolve)
    },
    //收货地址
    {
      path: '/addressList',
      name: 'addressList',
      meta: {title: '我的收货地址',keepAlive:false},
      component: resolve => require(['../components/address-list/address-list.vue'], resolve)
    },
    {
      path: '/addressAdd',
      name: 'addressAdd',
      meta: {title: '添加收货地址',keepAlive:false},
      component: resolve => require(['../components/address-add/address-add.vue'], resolve)
    },
    {
      path: '/addressEdit',
      name: 'addressEdit',
      meta: {title: '编辑收货地址',keepAlive:false},
      component: resolve => require(['../components/address-edit/address-edit.vue'], resolve)
    },
    //卡券中心
    {
      path: '/myCoupon',
      name: 'myCoupon',
      meta: {title: '我的卡券',keepAlive:false},
      component: resolve => require(['../coupon/my-coupon/my-coupon.vue'], resolve),
      children: [
        {
          path: '/validCoupon',
          name: 'validCoupon',
          meta: {title: '我的卡券',keepAlive:false},
          component: resolve => require(['../coupon/valid-coupon/valid-coupon.vue'], resolve)
        },
        {
          path: '/invalidCoupon',
          name: 'invalidCoupon',
          meta: {title: '我的卡券',keepAlive:false},
          component: resolve => require(['../coupon/invalid-coupon/invalid-coupon.vue'], resolve)
        }
      ]
    },
    //服务中心
    // {
    //   path: '/serviceCenter1,
    //   name: 'serviceCenter',
    //   meta: {title: '服务中心'},
    //   component: resolve => require(['../coupon/service-center/service-center.vue'], resolve),
    // },
    {
      path: '/serviceCenter',
      name: 'serviceCenter',
      meta: {title: '会员特权',keepAlive:true},
      component: resolve => require(['../coupon/service-center/service-center.vue'], resolve),
    },
    //卡券商品
    {
      path: '/couponGoods',
      name: 'couponGoods',
      meta: {title: '',keepAlive:false},
      component: resolve => require(['../coupon/coupon-goods/coupon-goods.vue'], resolve),
    }
  ]
})



